.preloader {
	width: 100vw;
	height: 100vh;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.loader {
	position: relative;
	width: 100px;
	height: 100px;
}

.loader:before, .loader:after {
	content: '';
	border-radius: 50%;
	position: absolute;
	inset: 0;
	box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.3) inset;
}

.loader:after {
	box-shadow: 0 2px 0 var(--preloader) inset;
	animation: loader 2s linear infinite;
}

@keyframes loader {
	0% {
		transform: rotate(0)
	}

	100% {
		transform: rotate(360deg)
	}
}